<template>
	<Modal_body :close="true" title="Upresniť cenu" class="modal-signature" >
		<!-- treba sem novu ikonku od borisa -->

		<!-- info -->
		<p class="mb-3">
			Cena je predbežne orientačná. <br>
			Aby sme Vám vedeli zobraziť presnú cenu, potrebujeme od Vás dodatočné údaje.
		</p>
		<p>
			Ak Vám postačuje orientačná cena, možte pokračovať bez doplnenia údajov. <br>
			Presná cena Vám bude dopočítaná pred uzatvorením zmluvy.
		</p>

		<!-- loop -->
		<div
			v-for="(field_data, field_id) in price_inputs"
			class="col-sm-12 mb-3"
		>
			<!-- label -->
			<label
				:for="field_id"
				class="input-label"
			>
				{{ field_data.label }}
			</label>

			<Text
				v-if="field_data.type === 'text'"
				@keyup.enter="specify_submit"
				emit_name="fake_emit"
				:field_id="field_id"
				:field_data="field_data"
			/>
			<p v-else >
				Specify_product_price modal currently supports 'text' input type only.
			</p>

			<!-- error -->
			<div
				v-for="(message, error_id) in price_errors[field_id]"
				class="validation-placeholder validation-message"
			>
				<span class="mark-circle mark-orange">!</span>
				{{ message }}
			</div>

		</div>
		<!-- END loop -->

		<!-- buttons -->
		<div class="text-center" >
			<button
				@click="specify_submit"
				:disabled="loading"
				class="btn btn-primary w-100 mt-3 mb-2"
				title="Na základe Vami doplnených údajov vieme zistiť finálnu presnú cenu."
			>
				Upresniť cenu
			</button>
			<button
				@click="skip_specify"
				:disabled="loading"
				v-show="allow_skip"
				class="btn btn-secondary w-100"
				title="Možte pokračovať s orientačnou cenou. Finálnu cenu prepočítame tesne pred uzatvorením zmluvy."
			>
				Pokračovať s orientačnou
			</button>
			<Bouncer_loading v-show="loading" size="large" />
		</div>

	</Modal_body>
</template>

<script>
	import Modal_body from '@/components/modals/parts/Modal_body.vue';
	import Bouncer_loading from '@/components/global/Bouncer_loading.vue';
	import STATE_modals from '@/store/STATE_modals.js';
	import STATE_params from '@/store/STATE_params.js';
	import Functions from '@/logic/Functions.js';
	import Forms_global from '@/logic/requests/Forms_global.js';
	import Product_helpers from '@/components/form_app/product/Product_helpers.js';
	import Text from '@/components/forms/basic_inputs/Text.vue';

	export default {
		props: {
		},
		components: {
			Modal_body,
			Bouncer_loading,
			Text,
		},
		emits: [],
		data() {
			return {
				launched_from: null, // from_global | from_product
				product_id: null, // or int
				product_name: null, // or str
				price_inputs: {}, // or obj
				price_errors: {}, // or obj
				loading: false,
			}
		},
		created() {
			this.launched_from = STATE_modals().retrieve_modal_data().launched_from;

			if (this.launched_from === 'from_global')
			{
				this.price_inputs = STATE_modals().retrieve_modal_data().price_inputs;
				this.price_errors = STATE_modals().retrieve_modal_data().price_errors;
			}
			else if (this.launched_from === 'from_product')
			{
				this.product_id = STATE_modals().retrieve_modal_data().product_id;
				this.product_name = STATE_modals().retrieve_modal_data().product_name;

				this.loading = true;
				Forms_global.get_global_form().then(
					(response) => {
						this.loading = false;
						this.price_inputs = Product_helpers.product_form_formatter(response.result.form, 'price');
					}
				);
			}
			else
			{
				console.log('unexpected launched_from', this.launched_from);
			}
		},
		methods: {
			skip_specify()
			{
				let input_tokens = Object.keys(this.price_inputs);
				STATE_params().reset_form_values();
				STATE_modals().set_modal_event_data('indicative_price');
				STATE_modals().close_them_all();
			},
			specify_submit()
			{
				this.loading = true;

				// serialize only global input values from State
				let form_data = Functions.serialize_specific_inputs(Object.keys(this.price_inputs));

				Forms_global.validate_global_form(form_data).then(
					(response) => {
						this.loading = false;

						if (response.result.validform === true) {
							STATE_modals().set_modal_event_data('specify_recalculate');
							STATE_modals().close_them_all();
						} else {
							console.log(response.result?.form_validation_error_elements);
							this.price_errors = response.result.form_validation_error_elements;
						}
					}
				);
			},
		},
		computed: {
			allow_skip()
			{
				return this.launched_from === 'from_product' && Object.keys(this.price_errors).length === 0;
			},
		},
		watch: {
		},
	}
</script>
