<template>
	<section
		v-show="info_shown"
		class="info_for_session"
		data-cy="info"
	>
		<h2 :title="print_group" >{{ heading }}</h2>
		<p title="endpoint" >{{ print_endpoint }}</p>
		<p title="receiver name (id)" >{{ print_receiver }}</p>
		<p title="session token" >{{ print_session_token }}</p>
		<p title="agent code" >{{ print_agent_code }}</p>
		<p title="filtred products" >{{ print_filtered_products }}</p>
		<p title="osmapp version" >{{ print_version }}</p>
		<p v-if="print_flow" title="step | state | finished | internal" >{{ print_flow }}</p>
	</section>
</template>

<script>
	import STATE_core from '@/store/STATE_core.js';
	import STATE_params from '@/store/STATE_params.js';
	import STATE_flow from '@/store/STATE_flow.js';
	import STATE_nav from '@/store/STATE_nav.js';
	import Constants from '@/logic/Constants.js';

	export default {
		props: {
		},
		components: {
		},
		emits: [],
		data() {
			return {
			}
		},
		created() {
		},
		methods: {
		},
		computed: {
			group_id()
			{
				return STATE_core().product_group_id();
			},
			heading() {
				if (STATE_core().get_run_mode() === 'node_red') {
					return 'node_red | ' + STATE_flow().get_workflow_name();
				}
				if (STATE_core().current_step() === 111) {
					return 'cookie session manager';
				}
				return Constants.group_name(this.group_id);
			},
			print_group()
			{
				return 'group name, for id: ' + this.group_id;
			},
			print_session_token()
			{
				return STATE_core().get_session_token() || '-';
			},
			print_agent_code()
			{
				return STATE_params().get_agent_code() || '-';
			},
			print_filtered_products()
			{
				return STATE_params().get_filtered_products() || '-';
			},
			print_version()
			{
				return STATE_core().give_app_version();
			},
			print_endpoint()
			{
				return STATE_core().get_endpoint_type();
			},
			print_receiver()
			{
				let receiver = STATE_params().get_receiver();
				return Constants.receiver_names(receiver) + ' (' + receiver + ')';
			},
			print_flow()
			{
				if (STATE_core().get_run_mode() !== 'node_red') {
					return false;
				}
				return STATE_flow().get_workflow_step() + ' | ' +
					STATE_flow().get_workflow_step_state() + ' | ' +
					( STATE_flow().get_workflow_finished() ? 'finished' : '' ) + ' | ' +
					( STATE_flow().get_workflow_internal() ? 'internal' : '' );
			},
			info_shown()
			{
				return !STATE_core().is_prod_endpoint() || STATE_params().capable_agent() || STATE_nav().show_info_section();
			},
		},
		watch: {
		},
	}
</script>
